import React from "react";
import "./Show.css";
import { Link } from "react-router-dom";

export default class Show extends React.Component {
  constructor() {
    super();
    this.state = {
      name: "",
    };
  }

  product1 = () => {
    this.setState({
      name: <Mobile1 />,
    });
  };
  product2 = () => {
    this.setState({
      name: <Mobile2 />,
    });
  };
  product3 = () => {
    this.setState({
      name: <Mobile3 />,
    });
  };
  product4 = () => {
    this.setState({
      name: <Mobile4 />,
    });
  };

  render() {
    return (
      <div>
        <nav className="navbar fixed-top navbar-expand-lg navbar-light bg-light">
          <Link className="navbar-brand" to="/">
            AK Mobile App
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item"></li>
              <li className="nav-item">
                <Link className="nav-link" to="/showproduct">
                  Show Product
                </Link>
              </li>
            </ul>
            <ul className="navbar-nav  ml-auto">
              <li className="nav-item">
                <Link className="nav-link" to="/">
                  Logout
                </Link>
              </li>
            </ul>
          </div>
        </nav>
        <div class="container cont1">
          <div>{this.state.name} </div>
        </div>
        <div className="container">
          {/* card */}
          <div className="card-deck">
            <div class="card" id="c1">
              <img
                className="showimg"
                src="\images\image8.png"
                alt=""
                onClick={this.product1}
              />

              <div class="card-body">
                <h5>IPhone 12 Pro</h5>
              </div>
            </div>
            <div class="card" id="c1">
              <img
                className="showimg"
                src="\images\image4.jpg"
                alt=""
                onClick={this.product2}
              />
              <div class="card-body">
                <h5>IPhone 12</h5>
              </div>
            </div>
            <div class="card" id="c1">
              <img
                className="showimg"
                src="\images\image7.jpg"
                alt=""
                onClick={this.product3}
              />
              <div class="card-body">
                <h5>OnePlus 8</h5>
              </div>
            </div>
            <div class="card" id="c1">
              <img
                className="showimg"
                src="\images\image6.jpg"
                alt=""
                onClick={this.product4}
              />
              <div class="card-body">
                <h5>OnePlMobile</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class Mobile1 extends React.Component {
  render() {
    return (
      <div>
        <div className="container">
          <div id="c2" className="card col-md-6">
            <img className="card-img-top" src="\images\image8.png" alt="" />
            <div className="card-body">
              <h4 className="card-title">IPhone 12 Pro</h4>
              <p className="card-text">
                ₹ 1,19,900 <br />
                6GB RAM <br />
                256GB Storage
                <br />
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
class Mobile2 extends React.Component {
  render() {
    return (
      <div>
        <div className="container">
          <div id="c2" className="card col-md-6">
            <img className="card-img-top" src="\images\image4.jpg" alt="" />
            <div className="card-body">
              <h4 className="card-title">IPhone 12</h4>
              <p className="card-text">
                ₹ 84,900 <br />
                4GB RAM <br />
                128GB Storage
                <br />
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
class Mobile3 extends React.Component {
  render() {
    return (
      <div>
        <div className="container">
          <div id="c2" className="card col-md-6">
            <img className="card-img-top" src="\images\image7.jpg" alt="" />
            <div className="card-body">
              <h4 className="card-title">OnePlus 8</h4>
              <p className="card-text">
                ₹ 44,999 <br />
                12GB RAM <br />
                256GB Storage
                <br />
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
class Mobile4 extends React.Component {
  render() {
    return (
      <div>
        <div className="container">
          <div id="c2" className="card col-md-6">
            <img className="card-img-top" src="\images\image6.jpg" alt="" />
            <div className="card-body">
              <h4 className="card-title">OnePlus 8T</h4>
              <p className="card-text">
                ₹ 42,999 <br />
                8GB RAM <br />
                128GB Storage
                <br />
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

